.productsHeading {
  margin: 2vmax auto;
  width: 15vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.171);
  padding: 2vmax;
  color: rgba(0, 0, 0, 0.678);
  font: 500 1.5vmax "Roboto";
  text-align: center;
}

.products {
  display: flex;
  flex-wrap: wrap;
  padding: 0 5vmax;
  justify-content: center;
  min-height: 30vh;
}

.paginationBox {
  display: flex;
  justify-content: center;
  margin: 6vmax;
  
}
.check-box{
font-size: 40px;
}
.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
}

.page-item {
  background-color: rgb(255, 255, 255);
  list-style: none;
  border: 1px solid rgba(0, 0, 0, 0.178);
  /* padding: 1vmax 1.5vmax; */
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 50px;
  width: 58px;
  height: 54px;
  padding-top: 13px;
  padding-left: 16px;
  margin-left: 9px;

}
.page-item:first-child {
border: 0px;
display: none;
}


.page-item:last-child {
  display: none;}
.page-link {
  text-decoration: none;
  font-size: 15px;
  font-weight: bold;
  color: black;
  transition: all 0.3s;
  background-color: transparent;
  border: 0px;

}

.page-item:hover {
  background-color: #ee2761;
}

.page-item:hover .page-link {
  color:white;
  background-color: transparent;
}

.pageItemActive {
  background-color:#ee2761;
}

.pageLinkActive {
  color: white;
}

.filterBox {
  width: 10vmax;
  position: absolute;
  top: 10vmax;
  left: 4vmax;
}

.categoryBox {
  padding: 0%;
}

.category-link {
  list-style: none;
  color: rgba(0, 0, 0, 0.61);
  font: 400 0.8vmax "Roboto";
  margin: 0.4vmax;
  cursor: pointer;
  transition: all 0.5s;
}
.category-link:hover {
  color: tomato;
}

.filterBox > fieldset {
  border: 1px solid rgba(0, 0, 0, 0.329);
}

@media screen and (max-width: 600px) {
  .filterBox {
    width: 20vmax;
    position: static;
    margin: auto;
  }

  .page-link {
    text-decoration: none;
    font-size: 15px;
    font-weight: bold;
    color: black;
    transition: all 0.3s;
    background-color: transparent;
    border: 0px;;
  }
  .category-link {
    font: 400 1.8vmax "Roboto";
  }
}
